body {
  background-image: url("light-gray-concrete-wall.8c2c9933.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  text-align: center;
  color: #1e7b97;
  text-shadow: 1px 2px #1e2b31;
  font-family: Lato, sans-serif;
  font-size: 2em;
  font-weight: 500;
}

.search-form {
  text-align: center;
  margin-bottom: 20px;
}

.search-form input[type="text"] {
  width: 300px;
  border: 3px solid #1e7b97;
  border-radius: 20px;
  padding: 10px;
  font-size: 16px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #57879e;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-family: Lato, sans-serif;
  font-size: 16px;
  box-shadow: 1px 3px #1e2b31;
}

.search-form:hover, .search-form:focus {
  cursor: pointer;
  color: #faebd7;
}

.gallery {
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 20px;
  display: grid;
}

.photo-card {
  background-color: #f9f9f9;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: auto;
  display: block;
}

.info {
  padding: 10px;
}

.info-item {
  margin: 0;
  font-size: 14px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #57879e;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  display: block;
}

